body{
  margin:0;
}
.grid-container {
  display: grid;
  grid-template-columns: auto auto auto;
  gap: 5px;
  background-color: rgb(0, 0, 0);
  padding: 5px;
  height: 100vh;
}

.grid-container > div {
  background-color: rgba(255, 255, 255, 0.8);
  text-align: center;
  padding: 20px 0;
  font-size: 30px;
}

.receipt {
  grid-row-start: 2;
  grid-row-end: 10;
  grid-column-start: 3;
  grid-column-end: 4;
}
.product {
  grid-row-start: 2;
  grid-row-end: 10;
  grid-column-start: 1;
  grid-column-end: 3;
}

.header{
  width: 100%;
  background: black!important;
  color:aliceblue;
  font-size: 30px;
  padding:10px;
  grid-column-start: 1;
  grid-column-end: 4;
  grid-row-start: 1;
  grid-row-end: 2;
}